@import url(https://fonts.googleapis.com/css?family=Roboto);
.NavBar-Container {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  border: 1px solid #000;
  background: #757575;
}

.no-background {
  border: none;
  background-color: transparent;
}

.NavBar-List ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
}

.NavBar-List__item {
  padding: 0 1rem;
}

.NavBar-List__item a {
  text-decoration: none;
  color: #fff;
}

.underline {
  text-decoration: underline;
}

.NavBar-List__item:hover {
  cursor: pointer;
  text-decoration: underline;
}

.Main-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  position: relative;
  /* clip-path: polygon(0 0, 100% 0, 100% 95%, 0% 100%); */
  background-image: url('https://picsum.photos/id/115/900/600/?blur=1');
  /* background-image: url('../../Images/background1.jpg'); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.Text-Container {
  visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(75, 75, 75, 0.95);
  padding: 0 2rem;
  border: 2px solid #fff;
  border-radius: 5px;
}

.show-text {
  visibility: visible;
}

.Text-Container p {
  color: #fff;
  font-size: 2.5rem;
  margin: 2rem 0;
}

.Main-Container__pointer {
  visibility: hidden;
  position: absolute;
  bottom: 2%;
  color: #fff;
  font-size: 1.2rem;
  line-height: 0;
}

.Main-Container__pointer .arrow {
  font-size: 2rem;
}
.show {
  visibility: visible;
}

.Projects-Section {
  min-height: 50vh;
  padding: 6rem 0;
}

#Projects-Section__header {
  text-align: center;
  text-decoration: underline;
  margin-bottom: 4rem;
  font-size: 3rem;
}

.Projects-Section__links {
  display: flex;
  justify-content: space-around;
}

.About-Container {
  height: auto;
  padding: 4rem 0;
  text-align: center;
}

h3 {
  text-decoration: underline;
  padding-bottom: 2rem;
}

.about-text {
  margin: 0 auto;
  font-size: 2rem;
  width: 44%;
  min-width: 22rem;
  color: #fff;
}

.Contact-Container {
  /* height: 100vh; */
  /* background: #eee; */
  height: auto;
  padding: 1rem 0 6rem;
}

.Contact-header {
  margin: 6rem 0 4rem;
  text-align: center;
  text-decoration: underline;
}

.Form-Container {
  border: 1px solid #000;
  background: #fff;
}

.sendingMsgText {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: red;
}

/* .sendingMsgText p {
  font-size: 1.5rem;
  color: red;
} */

.center-field {
  text-align: center;
}

.input-field small {
  color: crimson;
  margin: 0.2rem 0;
  display: block;
}

.hidden {
  visibility: hidden;
}

.feedback {
  padding-bottom: 0.2rem;
}

.feedback small {
  margin: 0.2rem 0;
  display: block;
}

.main-error {
  color: crimson;
  text-align: center;
}

.success {
  color: lime;
  text-align: center;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: #333;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}

h3 {
  color: #ddd;
}

.toast-style {
  border: 1px solid #fff;
  background: #757575;
}

@media only screen and (min-width: 601px) {
  .container {
    width: 95%;
  }
}
@media only screen and (min-width: 1082px) {
  .container {
    width: 90%;
  }
}

